    font-weight: bold;
    font-size:16px;
  }

  .dapps{
    width: 1100px;
    height: 410px;
    margin: 0 auto;
    img {
      width: 150px;
      height: 150px;
    }
    .el-row {
      margin-top: 40px;
    }
    /*dapps图标控制*/
    .el-row .el-col{
      float: left;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      width: 16%;
      margin-bottom: 30px;
    }
  }
  .dapps-logo{
    text-align: center;
    width: 200px;
    height: 175px;
    background: #1F2937;
    box-shadow: 0px 3px 10px 0px rgba(153, 153, 153, 0.1);
    border-radius: 20px;
    display: table-cell;
    vertical-align: middle;
    img{
      width: 100px;
      height: 100px;
      border-radius: 25px;
    }
  }
  .right-move{
    margin-right: 30px;
  }
  /*左右箭头样式*/
  .dapps-left-arrow{
    position: absolute;
    bottom:170px;
    left: 5px;
    img{
      width: 45px;
      height: 90px;
    }
    img:hover{
      content: url(assets/images/icon/dapps_left_pre.png);
      cursor: pointer;
    }
  }
  .dapps-right-arrow{
    position: absolute;
    bottom:170px;
    right: 15px;
    img{
      width: 45px;
      height: 90px;
    }
    img:hover{
      content: url(assets/images/icon/dapps_right_pre.png);
      cursor: pointer;
    }
  }
  .upload-dapp{
    height: 90px;
    background: #1F2937;
    box-shadow: 0px 3px 10px 0px rgba(153, 153, 153, 0.2);
    border-radius: 55px;
    padding-top: 40px;
  }
  .footer {
    background: #1F2937;
    margin-top: 50px;
    padding-bottom: 400px;
    color: #8590A6;
    font-weight: bold;
    font-size: 14px;
    font-family: Microsoft YaHei;
    p{
      font-size: 24px;
      margin-bottom: 0;
      &:nth-child(2){
        font-size: 14px;
      }
    }
    ul{
      padding-left: 0px;
      li{
        list-style: none;
        display:inline-block;
        margin-top: 0px;
        margin-right: 20px;
        a{
          color: #8590A6;
          &:hover{
            color: #a44238;
          }
        }
      }
    }
  }
  .footer-content {
    padding-top: 80px;
  }
  /*备案号样式*/
  .record-number{
    a{
      color: rgba(99, 99, 99, 0.75);
      font-size: 10px;
    }
  }
  .QR{
    display: table-cell;
    vertical-align: middle;
    text-align: center;
  }
  /*二维码*/
  .QRCode {
    text-align: center;
    li {
      margin: 0 !important;
    }
  }
  .phone-number {
    font-size: 38px !important;
    font-weight: normal !important;
    margin-top: 10px !important;

  }
  .QRCodeImg {
    width: 100px;
    height: 100px;
  }

</style>



